<template>
  <span>
    <Modal
      v-model="showDialog"
      width="500"
      title="/"
      :mask-closable="false">
      <div>
        <p>{{course.course_name}}</p>

        <div style="margin-top: 20px;margin-bottom: 20px;">
          <Button v-if="course.course_number" v-for="num in course.course_number"
                  type="success" @click="uploadVideoNum = num" style="margin: 5px;">{{num}}</Button>
          <Button type="success" @click="uploadVideoNum = course.course_number + 1">{{course.course_number + 1}}</Button>
        </div>

        <Upload :action="'/api/ilearning/uploadVideo?id=' + course.id + '&video_number=' + uploadVideoNum" :on-success="uploadComplete">
          <Button icon="ios-cloud-upload-outline">Upload files</Button>
          <span v-if="uploadVideoNum > 0" style="color: #3300ff;">*{{uploadVideoNum}}</span>
          <span v-else style="color: red;"></span>
        </Upload>
      </div>
    </Modal>

    <a href="javascript:;" style="color:#f55e13;font-family: Arial;font-weight: 700;" @click="showDialog=true">/</a>
  </span>
</template>

<script>
  export default {
    name: "UploadVideo",
    // 
    props:["course"],
    data(){
      return {
        showDialog:false,
        // 
        uploadVideoNum:-1,
      }
    },
    methods:{
      uploadComplete(res, file) {
        if(res.status=="SUCCESS"){
          this.$Notice.success({
            title: '',
            desc: "!"
          });
          this.$emit('uploadComplete');
        }else{
          this.$Notice.error({
            title: '',
            desc: ' ' + file.name + ' !'
          });
        }
      },
    }
  }
</script>

<style scoped>

</style>



