<template>
  <div>
    <Row>
      <!--  -->
      <Col span="14" style="padding-right: 10px;">
        <Input v-model="reply_content" type="textarea" :rows="8" placeholder="Enter something..." />
        <Button type="success" style="margin-top: 5px;float: right;" @click="submitComment"></Button>
      </Col>
      <Col span="10" style="border: 1px solid #e9e9e9;font-size:12px;padding: 10px;">
        <p></p>
        <p>1</p>
        <p>2</p>
        <p>3</p>
        <p>4</p>
        <p>5</p>
      </Col>
    </Row>
  </div>
</template>

<script>
  import {AddCommentReply} from "../../../api/index"

  export default {
    name: "CommentForm",
    // 
    props:["parent_id", "comment_id", "comment_type", "refer_user_name"],
    data(){
      return {
        reply_content:"",
      }
    },
    methods:{
      submitComment: async function () {
        var reply_content = this.submit_comment;
        const result = await AddCommentReply(this.parent_id, this.reply_content, this.comment_id, this.comment_type, this.refer_user_name);
        if(result.status=="SUCCESS"){
          //  refreshCommentReply 
          this.$emit('refreshCommentReply');
        }
      }
    }
  }
</script>

<style scoped>

</style>



