<template>
  <div>
    <!-- , -->
    <Affix>
      <Menu mode="horizontal" :theme="theme1" active-name="1" style="padding-left: 650px;">
        <Submenu name="1">
          <template slot="title">
            <Icon type="ios-stats" />
            
          </template>
          <MenuGroup title="">
            <MenuItem name="1-1"><router-link to="/iblog/blog_list"></router-link></MenuItem>
            <MenuItem name="1-2"><router-link to="/iblog/catalog_add">/</router-link></MenuItem>
            <MenuItem name="1-3"><router-link to="/iblog/blog_add">/</router-link></MenuItem>
            <MenuItem name="1-4"></MenuItem>
          </MenuGroup>
          <MenuGroup title="">
            <MenuItem name="1-5"><router-link to="/ilearning/index"></router-link></MenuItem>
            <MenuItem name="1-6"><router-link to="/inote/index"></router-link></MenuItem>
          </MenuGroup>
          <MenuGroup title="">
            <MenuItem name="1-7"><router-link to="/ifile/ifile">IFile </router-link></MenuItem>
            <MenuItem name="1-8"><router-link to="/ifile/ifile">IFile </router-link></MenuItem>
          </MenuGroup>
        </Submenu>
        <MenuItem name="2">
          <Icon type="ios-construct" />
          
        </MenuItem>
        <Submenu name="3">
          <template slot="title">
            <Icon type="ios-paper" />
            
          </template>
          <MenuGroup title="">
            <MenuItem name="3-1" @click.native="$router.push({path: '/ilearning/configuration'})"></MenuItem>
          </MenuGroup>
        </Submenu>
        <Submenu name="4">
          <template slot="title">
            <Icon type="ios-people" />
            <span v-if="loginUserName">{{loginUserName}}</span>
            <span v-else></span>
          </template>
          <MenuGroup title="">
            <MenuItem name="4-1" @click.native="cancelUser"></MenuItem>
            <MenuItem name="4-2" @click.native="cancelUser"></MenuItem>
          </MenuGroup>
        </Submenu>
      </Menu>
    </Affix>
  </div>
</template>

<script>
  import {getCookie} from '../../../tools/index'
  import {delCookie} from '../../../tools/index'

  export default {
    name: "Header",
    data () {
      return {
        theme1: 'light',
        loginUserName:'',
      }
    },
    methods:{
      cancelUser() {
        delCookie("userName");
        this.loginUserName = "";
        window.location.href = "/api/auth/redirectToLogin/?redirectUrl=" + window.location.href;
      }
    },
    mounted:function(){
      this.loginUserName = getCookie("userName");
    },
  }
</script>





